.direction__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  padding-top: 30px;
  flex-direction: row;
  justify-content: space-between;

  @include viewport--sm {
    display: none;
  }
}

.direction__item {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding-right: 10px;
}

.direction__title {
  margin: 0;
  font-size: 16px;
  text-align: center;
  color: #606060;
  max-width: 272px;
  width: 100%;
  height: 100%;
  border-bottom: 1px solid $color-blue;
  padding-bottom: 10px;
}

.direction__data {
  text-align: center;
  font-size: 14px;
  color: $color-blue;
  padding-top: 8px;
  font-weight: 600;
}

.direction__status {
  text-align: center;
  font-size: 14px;
  font-weight: 600;

  &--ending {
    color: $color-red;
  }

  &--available {
    color: $color-green;
  }

  &--waiting {
    color: $color-gray;
  }
}

.direction__arrow {
  outline: none;
  align-self: center;
}

.direction__arrow,
.swiper-button-next::after {
  content: none;
}

.direction__arrow,
.swiper-button-prev::after {
  content: none;
}

.direction__arrow--left {
  margin-right: 19px;
}

.direction__arrow--right {
  margin-right: 9px;
}

.direction__list,
.swiper-slide {
  height: inherit;
}
